@page {
  size: A4 portrait;
  margin: 10mm;
}
html,
body {
  background: #f3f3f3;
  margin: 0;
  padding: 0;
}

/* 上方控制列樣式（只在螢幕上顯示） */
.a4-wrapper {
  padding-top: 10px;
}
.controls {
  width: 210mm;
  margin: 0 auto 10px auto;
  font-size: 14pt;
}
.controls label {
  margin-left: 8px;
}
.note {
  color: red;
  font-size: 14pt;
}

/* 列印時隱藏控制區與按鈕等 UI 元件 */
@media print {
html,
  body {
    background: transparent;
}
.page {
    margin: 0;
    border: none;
}
button, .note,
  .no-print {
    visibility: hidden !important;
    display: none !important;
}
}
